<template>

  <div>
<header class="header"><a  v-on:click="backQrcodeView"><img src="static/back.svg" alt=""/></a>
      <h1>Operação</h1><img src="" alt=""/>
    </header>
    <main class="content">
      <div class="box">
        <div class="msgs red"><img src="static/i-close.svg" alt="">
          <p>A operação falhou! <br> Tente novamente em alguns minutos.</p>
        </div>
      </div>
    </main>
    <footer class="footer"><a  v-on:click="back" >TENTAR NOVAMENTE</a></footer>
  </div>
</template>
<script>
import ProviderDevice from "@/resources/provider/provider-device";

export default {
  name: "NotFound",
  components: {},
  created() {},
  mounted() {
    // this.$router.push("/home");
  },
  methods: {
    back: function() {
      ProviderDevice.backWebview()
    },
    backQrcodeView: function(){
      ProviderDevice.openQrcodeView()
    }
  }
};
</script>



