<style src="./home.css"></style>
<template src="./home.html"></template>
<script>
import ProviderDialog from "@/resources/provider/provider-dialog";

export default {
  name: "home",
  components: {},
  data() {
    return {};
  },

  created() {
    let tipo = this.$route.query.tipo;
    let qrcode = this.$route.query.code;      
    let myThis = this 

    switch (tipo) {
      case "CODIGOBARRA":
        this.consultarBarraCodigo(qrcode);
        break;
      case "QRCODE":
        this.consultarQrcodeSession(qrcode);
        break;
      case "CAMERA-NATIVA-QRCODE":
        let decodeString = decodeURIComponent(qrcode)
        let operacao = JSON.parse( decodeString )
          this.rotina( myThis, operacao )
        break;        
      default:
        this.$router.push("/");
        break;
    }
  },
  mounted() {},
  methods: {
    consultarBarraCodigo(codigobarra) {
      let url = this.URL_BANK_REQUEST_CODIGO_BARRA + codigobarra;
      console.log(codigobarra);

      let rota = "/home/bank-slip";

      this.$http
        .get(url)
        .then(response => {
          let operacao = response.data;
          this.$router.push({
            path: rota,
            query: { operacao: JSON.stringify(operacao) }
          });
        })
        .catch(error => {
          console.log(error);
          this.$router.push("/");
        });
    },
    consultarQrcodeSession: function(qrcodeSession) {
      let myThis = this 
      let rota = "/";      

      this.$http.get(qrcodeSession)
        .then(response => {
          let operacao = response.data;
          this.rotina( myThis, operacao )
        })
        .catch(error => {
          console.log(error);
          this.$router.push(rota);
        });
    },
    rotina( myThis, operacao ) {
      let rota = "/";      
       switch (operacao.tipoOperacao) {
            case "PAGAMENTO":
              rota = "/home/payment";
              break;
            case "TRANSFERENCIA":
              rota = "/home/transfer";
              break;
            case "BOLETO-BANCARIO":
              rota = "/home/bank-slip";
              break;
            case "CONVENIO":
              rota = "/home/bank-slip";
              break;
            default:
              break;
          }

          myThis.$router.push({
            path: rota,
            query: { operacao: JSON.stringify(operacao) }
          });
    }
  }
};
</script>



