<style src="./payment.css"></style>
<template src="./payment.html"></template>
<script>
export default {
  name: "payment",
  components: {},
  data() {
    return {
      template: {
        beneficiario: {},
        valor: Math.floor(Math.random() * Math.floor(400)),
        juros: 0,
        encargos: 0,
        multa: 0
      },
      operacao: {}
    };
  },

  created() {
    let query = this.$route.query.operacao;
    this.operacao = query ? JSON.parse(query) : query;
  },
  mounted() {
    this.$root.$emit("menu_superior_titulo", "Pagamento");

    // abre o modal
    $(document).ready(function() {
      $(".bot-lista").click(function() {
        // $(".overlay").slideToggle("slow");
        $(".overlay")
          .delay(100)
          .fadeIn(500);
      });
    });

    // fecha o modal
    $(document).ready(function() {
      $(".bot-exit").click(function() {
        $(".overlay")
          .delay(100)
          .fadeOut(500);
      });
      $(".overlay").click(function() {
        $(".overlay")
          .delay(100)
          .fadeOut(500);
      });
    });
  },
  methods: {
    next: function() {
      this.$router.push({
        path: "/home/address",
        query: { operacao: JSON.stringify(this.operacao) }
      });
    },
    register: function() {
      if (this.operacao.exige_endereco) {
        this.$router.push({
          path: "/home/address",
          query: { operacao: JSON.stringify(this.operacao) }
        });
      } else {
        let myThis = this;
        let rota = "/";
        let urlPath =
          this.URL_BANK_DEMO +
          "/operacoes/" +
          this.operacao._id +
          "/autorizacao";
        let autorizacao = {
          operacaoAutorizada: true,
          dataHoraAutorizacao: new Date()
        };

        this.$http
          .post(urlPath, autorizacao)
          .then(response => {
            let operacao = response.data;
            myThis.$router.push("/home/form-detail");
          })
          .catch(error => {
            console.log(error);
            this.$router.push(rota);
          });
      }
    }
  }
};
</script>



