<style src="./transfer.css"></style>
<template src="./transfer.html"></template>
<script>
export default {
  name: "transfer",
  components: {},
  data() {
    return {
      operacao: {},
      isTED: false
    };
  },

  created() {
    let query = this.$route.query.operacao;
    this.operacao = JSON.parse(query);
 

    $(function() {
      $(".imputMonetario").maskMoney();
    });
  },
  mounted() {
    this.$root.$emit("menu_superior_titulo", "Transferência");
  },
  methods: {
    register: function() {
      this.$router.push("/home/form-detail");
    },
    ColorTipoOperacao: function() {
      return isTED ? { color: "red" } : { color: "black" };
    }
  }
};
</script>



