<template src="./menu-lateral.html"></template>
<style src="./menu-lateral.css"></style>
<script>
export default {
  name: "menu-lateral",
  props: {
    informacao: { 
      nome : 'Leonardo Gearq',
      email : 'leonardo@sicoob.com.br',
      numeroTelefonico : '06199999999'
    }
  },
  computed: {},
  data() {
    return {  };
  },
  created() {  },
  mounted() {
  //  window.onresize = function(event) { 
      if (screen.width <= 500) {
        $(".button-collapse").sideNav({
          menuWidth: 300, // Default is 300
          draggable: true
          //draggable: true
        });
      } else {
        $(".button-collapse").sideNav({
          menuWidth: 220, // Default is 300
          draggable: true
          //draggable: true
        });
      }
    //}
  },
  methods: {
     
    toggleMenu(event) {
      $(".button-collapse").sideNav("hide");
    },
    loginOff: function() {
      $(".button-collapse").sideNav("hide");
      var that = this;
      console.log("loginOff");
      this.$session.destroy();
      this.$root.$emit("popUpLoading", true);
      setTimeout(function() {
        that.$router.push("/");
        that.$root.$emit("popUpLoading", false);
      }, 900);
      //  MyToken.revokeToken( this )
      //   .then(
      //     sucess => { this.$session.destroy(); } ,
      //     error =>  { this.$session.destroy(); }
      //   );
    },
    telaUsuario(){
        this.$router.push("/home/usuario");
    }
  }
};
</script>
 
 


