var eventoIOSQRCODE = document.createEvent("CustomEvent")
import whoisThisMobile from "@/componentes/config/whoisThisMobile"
import router from "@/componentes/config/routes.js";

class ProviderDevice {

    static Imei() {
        
        return new Promise((resolve, reject) => {
             
            switch( whoisThisMobile() ) {
                case 'ANDROID': 
                
                let stringQrcode = ContainerFunctions.Imei()
                resolve( stringQrcode ) 

                break;
                case 'IOS':
                eventoIOSQRCODE.initEvent("change", false, true)
                window.addEventListener('change', function (e) {
                    if (e != undefined) {

                        let stringQrcode =  e.detail
 
                        resolve(stringQrcode)
                    }

                }, false);
                // CHAMADA  webkit IOS .
                    window.webkit.messageHandlers.OpenCameraQrcode.postMessage("");
                break; 

                case "OUTRO" :
                resolve('458240051111110') 
                break
            }
            
    
        })
    
    }
    static FcmToken( ) {
        
        return new Promise((resolve, reject) => {
             
            switch( whoisThisMobile() ) {
                case 'ANDROID': 
                
                let stringQrcode = ContainerFunctions.FcmToken()
                resolve( stringQrcode ) 

                break;
                case 'IOS':
                eventoIOSQRCODE.initEvent("change", false, true)
                window.addEventListener('change', function (e) {
                    if (e != undefined) {

                        let stringQrcode =  e.detail
 
                        resolve(stringQrcode)
                    }

                }, false);
                // CHAMADA  webkit IOS .
                    window.webkit.messageHandlers.FcmToken.postMessage("")
                break; 

                case "OUTRO" :
                resolve('dKK0FjisZSw:APA91bGHQbqEwoJCUtJEd3iBoxczSB2E8SqgqvBBReRzzMRi5NO83GmMDxa1sUOE_nDt6MqJJfgvQzedMN6XPR1qtrr50-nG_cz8VTHpuyIwT0oo5pa58Bk4hEU2tkHF8PkjSAMvtTgp') 
                break
            }
            
    
        })
    
    }
    static autenticarPorTelefone( numeroTelefonico ) {
        
        return new Promise((resolve, reject) => {
             
            switch( whoisThisMobile() ) {
                case 'ANDROID': 
                
                let stringQrcode = ContainerFunctions.autenticarPorTelefone( numeroTelefonico )
                resolve( stringQrcode ) 

                break;
                case 'IOS':
                eventoIOSQRCODE.initEvent("change", false, true)
                window.addEventListener('change', function (e) {
                    if (e != undefined) {

                        let stringQrcode =  e.detail
 
                        resolve(stringQrcode)
                    }

                }, false);
                // CHAMADA  webkit IOS .
                    window.webkit.messageHandlers.autenticarPorTelefone.postMessage( numeroTelefonico )
                break; 
                case "OUTRO" :
                resolve('VALIDADO') 
                break
            }
            
    
        })
    
    }
    static codigoSMS( key ) {
        
        return new Promise((resolve, reject) => {
             
            switch( whoisThisMobile() ) {
                case 'ANDROID': 
                
                let result = ContainerFunctions.codigoSMS( key )
                resolve( result ) 

                break;
                case 'IOS':
                eventoIOSQRCODE.initEvent("change", false, true)
                window.addEventListener('change', function (e) {
                    if (e != undefined) {

                        let result =  e.detail
 
                        resolve(result)
                    }

                }, false);
                // CHAMADA  webkit IOS .
                    window.webkit.messageHandlers.codigoSMS.postMessage( key )
                break; 
                case "OUTRO" :
                resolve('VALIDADO') 
                break
            }
            
    
        })
    
    }
    static vibrar(  timeVibra ) {
        
        switch( whoisThisMobile() ) {
            case 'ANDROID': 
                ContainerFunctions.deviceVibrar( timeVibra );
            break;
            case 'IOS':
                window.webkit.messageHandlers.deviceVibrar.postMessage( timeVibra );
            break; 
        }
    
    }
    static esconderTeclado() {
        
        switch( whoisThisMobile() ) {
            case 'ANDROID': 
                ContainerFunctions.deviceEsconderTeclado();
            break;
            case 'IOS':
                window.webkit.messageHandlers.deviceEsconderTeclado.postMessage("");
            break; 
            case 'OUTRO':  break;

        }
    
    }
    static backWebview() {
        switch( whoisThisMobile() ) {
            case 'ANDROID': 
                ContainerFunctions.backWebview();
            break;
            case 'IOS':
                window.webkit.messageHandlers.backWebview.postMessage("");
            break; 
            case 'OUTRO':  
            router.go(-1 ) 
            break;

        }
    }
    static openQrcodeView() {
        switch( whoisThisMobile() ) {
            case 'ANDROID': 
                ContainerFunctions.openQrcodeView();
            break;
            case 'IOS':
                window.webkit.messageHandlers.openQrcodeView.postMessage("");
            break; 
            case 'OUTRO':  
            router.go(-1 ) 
            break;

        }
    }    
}


module.exports = ProviderDevice



