'use strict';

const axiosRetry = require('axios-retry');



var config = function (component) {

    axiosRetry(component.$http, { retries: 10 ,  shouldResetTimeout : true , retryDelay : (retryCount) => {
        return retryCount * 2000;
    } });


    component.$http.interceptors.request.use(function (config) {
        component.erros = [];
        component.$root.$emit("popUpLoading", true)

        return config;
    }, function (error) {
        component.$root.$emit("popUpLoading", false)
        return Promise.reject(error);
    });

    component.$http.interceptors.response.use(function (response) {
        component.$root.$emit("popUpLoading", false)

        return response;

    }, function (error) {

        let response = error.response
        component.$root.$emit("popUpLoading", false)
        if (response.status === 400) {
            return Promise.reject(error)

        } else {
            return Promise.reject(error)
        }
        return Promise.reject(error)
    });
}

export default config;


