import VueRouter from 'vue-router';



import NotFoudView from '@/componentes/404.vue'
import DashboardView from '@/componentes/dashboard/dashboard.vue'
import HomeView from '@/componentes/dashboard/home/home.vue'
import TransferView from '@/componentes/dashboard/transfer/transfer.vue'
import BankSlipView from '@/componentes/dashboard/bank-slip/bank-slip.vue'
import FormDetailView from '@/componentes/dashboard/form-detail/form-detail.vue'
import PaymentView from '@/componentes/dashboard/payment/payment.vue'
import FormPaymentView from '@/componentes/dashboard/form-payment/form-payment.vue'

import PaymentPassView from '@/componentes/dashboard/payment-pass/payment-pass.vue'
import AddressView from '@/componentes/dashboard/address/address.vue'
import AddressCreateView from '@/componentes/dashboard/address-create/address-create.vue'


const routes = [
 


  {
    path: '/home',
    component: DashboardView,
    children: [

      {
        path: '',
        component: HomeView,
        name: 'home',
        meta: {
          description: 'Menu dashboard'
         }
      },
      {
        path: 'transfer',
        component: TransferView,
        name: 'transfer',
        meta: {
          description: 'Form by tranfer bank money '
         }
      },
      {
        path: 'bank-slip',
        component: BankSlipView,
        name: 'bank-slip',
        meta: {
          description: 'Form by pay bank slip'
         }
      },
      {
        path: 'form-payment',
        component: FormPaymentView,
        name: 'form-payment',
        meta: {
          description: 'Form by create payments with the protocol qrpague'
         }
      },
      {
        path: 'form-detail',
        component: FormDetailView,
        name: 'form-detail',
        meta: {
          description: 'Form detail response'
         }
      },

      {
        path: 'payment',
        component: PaymentView,
        name: 'payment',
        meta: {
          description: 'Form payment request with a identify qrcode '
         }
      },
      {
        path: 'address',
        component: AddressView,
        name: 'address',
        meta: {
          description: 'Form  escolha de endereco '
         }
      },
      {
        path: 'address-create',
        component: AddressCreateView,
        name: 'address-create',
        meta: {
          description: 'Form  escolha de endereco '
         }
      },
      {
        path: 'payment-pass',
        component: PaymentPassView,
        name: 'payment-pass',
        meta: {
          description: 'Form for pass authentication rules  '
         }
      }


    ]
  },

  {
    path: '*',
    component: NotFoudView
  }
]


const router = new VueRouter({
  routes
});

// router.beforeEach((to, from, next) => {
//   if (to.meta.requiredAuth) {
//     let situacao = LoginSession.situacao(router.app)
//     if (situacao) {
//       next()
//     } else {
//       router.push('/')
//     }
//   } else {
//     next()
//   }
// })
export default router;


