import whoisThisMobile from "@/componentes/config/whoisThisMobile"

class ProviderDialog {
 

    static toast( msg ) {
        
        switch( whoisThisMobile() ) {
            case 'ANDROID': 
                ContainerFunctions.showToast( msg );
            break;
            case 'IOS':
                window.webkit.messageHandlers.showToast.postMessage( msg );
            break; 
            case 'OUTRO': alert( msg ); break;
        }
    
    }

}

module.exports = ProviderDialog



