<style src="./bank-slip.css"></style>
<template src="./bank-slip.html"></template>
<script>
export default {
  name: "bank-slip",
  components: {},
  data() {
    return {
      operacao: {},
      template: {
        beneficiario: {},
        valor: Math.floor(Math.random() * Math.floor(400)),
        juros: 0,
        encargos: 0,
        multa: 0
      }
    };
  },

  created() {
    let query = this.$route.query.operacao;
    this.operacao = JSON.parse(query);
    $(function() {
      $(".imputMonetario").maskMoney();
    });    
  },
  mounted() {
    this.$root.$emit(
      "menu_superior_titulo",
      "INFORMAÇÕES DO " + this.tipo(this.operacao.tipoOperacao)
    );
  },
  methods: {
    register: function() {
      this.$router.push("/home/form-detail");
    },
    tipo: function(key) {
      switch (key) {
        case "BOLETO-BANCARIO":
          return "TÍTULO";
          break;
        case "CONVENIO":
          return "CONVÊNIO";
          break;
      }
    },
    LinhaDitavel(key, codigobarra) {
      switch (key) {
        case "BOLETO-BANCARIO":
          var valor = parseFloat(  codigobarra.substring(codigobarra.length - 10, codigobarra.length) ) / 100 ;
          return valor;
        case "CONVENIO":
          return 138.00
          break;
      }
    },

    DataVencimento(key, codigoBarra) {
      switch (key) {
        case "BOLETO-BANCARIO":
          var vencimento = codigoBarra.slice(33, 37);
          var date = new Date("10/07/1997");
          date.setTime(date.getTime() + vencimento * 24 * 60 * 60 * 1000);
          return date
          // return (
          //   ("0" + date.getDate()).slice(-2) +
          //   "/" +
          //   ("0" + (date.getMonth() + 1)).slice(-2) +
          //   "/" +
          //   date.getFullYear()
          // );
          return valor;
        case "CONVENIO":
          return new Date("10/03/2019");
          break;
      }
    }
  }
};
</script>



