<style src="./menu-superior.css"></style>
<template src="./menu-superior.html"></template>
<script>
import ProviderDevice from "@/resources/provider/provider-device";

export default {
  name: "menu-superior",
  components: {},
  data() {
    return {
      menuSuperiorTitulo: "QRPAGUE",
      menus: []
    };
  },

  created() {
    let that = this;
    this.$root.$on("menu_superior_titulo", function(titulo) {
      that.menuSuperiorTitulo = titulo;
    });
  },
  mounted() {},
  methods: {
    toggleMenu: function(event) {
      $(".button-collapse").sideNav("show");
    },
    back: function() {
      ProviderDevice.backWebview();
    },
    backQrcodeView: function() {
      ProviderDevice.openQrcodeView();
    }
  }
};
</script>



