<style src="./form-payment.css"></style>
<template src="./form-payment.html"></template>
<script>
export default {
  name: "form-payment",
  components: {},
  data() {
    return {
      operacao : {
        valor : 0 
      }
    }
  },

  created() {
    
  
  },
  mounted() {
    this.$root.$emit("menu_superior_titulo", "Pagamento");
 
  },
  methods: {
    next: function(){
         this.$router.push({
            path: "/home/address",
            query: { operacao: JSON.stringify(this.operacao) }
          });
    },
    setValue: function( value ) {
      if ( !this.operacao.valor || this.operacao.valor.toFixed(2).toString().length < 11)
      this.operacao.valor = (this.operacao.valor)*10 + (value/100) 
    },
    backValue: function() {
      // let valor = this.operacao.valor
        // this.operacao.valor = valor - Number(valor.toString()[valor.toString().length-1])/100
        this.operacao.valor = 0 
    },

    register: function(){

    }
  }
};
</script>



