class Storage {
    static load(myThis, tag) {
        let retorno = myThis.$session.get(tag)
        setTimeout(function () { }, 200);

        if (retorno && retorno.length > 0) {
            let obj = JSON.parse(retorno)
            return obj;

        }
        return null
    }
    static read(myThis, tag) {
        return new Promise((resolve, reject) => {

            for (var t = 0; t < 10; t++) {
                let retorno = myThis.$session.get(tag)
                setTimeout(function () { }, 2000);

                if (retorno && retorno.length > 0) {
                    let obj = JSON.parse(retorno)
                    resolve(obj);

                }
            }
            reject({});


        })

    }

    static write(myThis, tag, obj) {

        return new Promise((resolve, reject) => {
            try {
                myThis.$session.start()

                let jsonString = JSON.stringify(obj)

                myThis.$session.set(tag, jsonString);
                setTimeout(function () {
                    resolve(true)
                }, 1000);

            } catch (erro) {
                reject(erro)
            }
        })

    }


}

module.exports = Storage


