<script>
import Vue from "vue";
import accounting from "accounting";
import DateFormat from "dateformat";

Vue.filter("formatarCodigo", function(codigo) {
  var str = "" + codigo;
  var pad = "00000000";
  var ans = pad.substring(0, pad.length - str.length) + str;
  return ans;
});

Vue.filter("formatarDataTime", function(data) {
  return DateFormat(data, "dd/mm HH:MM:ss");
});
Vue.filter("formatarData", function(data) {
  return DateFormat(data, "dd/mm/yyyy");
});

Vue.filter("currency", function(val, precision, prefix) {
    let n = Math.pow( 10 , precision )
    let totalValor = Math.trunc( val * n ) / n;
   return accounting.formatMoney(totalValor, prefix + " ", precision, ".", ",");
});
 
</script>



