<style src="./address.css"></style>
<template src="./address.html"></template>
<script>
import ProviderDialog from "@/resources/provider/provider-dialog";

export default {
  name: "address",
  components: {},
  data() {
    return {
      operacao: {}
    };
  },

  created() {
    let query = this.$route.query.operacao;
    this.operacao = query ? JSON.parse(query) : query;
  },
  mounted() {},
  methods: {
    endereco: function() {
      this.$router.push("/home/address-create");
    },
    register: function() { 
               this.$router.push({
          path: "/home/payment-pass",
          query: { operacao: JSON.stringify(this.operacao) }
        });
    }
  }
};
</script>



